riot.mixin('spcmixin', {
	init: function() {
		this.on('mount update updated', function() {
			var comps = this.root.querySelectorAll('[class*="spc-js-"');
			if (comps.length > 0) {
				window.componentHandler.upgradeElements(comps);
			}
		});
	}
});
